
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

*{
  padding: 0;
  margin: 0;
  outline: 0;
  list-style: none;
  vertical-align:baseline;
  font: 12px/22px Verdana,Arial,sans-serif,"Times New Roman",思源宋体;
}
.root{
  height: 60px;
  width: 100%;
  background: lavender;
  text-align: center;
}

.header-layout-default{
  height: 100%;
  width: 100%;
  text-align: left;
}
.header-layout-default>div{
  position: relative;
  display: inline-block;
  height: 100%;
  margin: 0 10px;
}
.header-layout-default>div>div{
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.header-layout-default+div{
  min-height: calc(100vh - 60px);
  background: aliceblue;
}

div.logo{
    height: 100%;
    width: 60px;
    margin-left: 50px;
    cursor: pointer;
}
.logo img{
    display: inline-block;
    height: 100%;
}

.search{
    width: 200px;
}
.search .ivu-input-suffix{
    background:#2d8cf0;
    border-radius:0 4px 4px 0;
}

.menu .menu-item{
    max-width: 150px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: break-all;
}
.menu .menu-item-span{
    display: inline-block;
    height: 100%;
}

div.lang{
    float: right;
    width: 120px;
    text-align: left;
}
.lang:hover{
    background: ghostwhite;
}
.lang .lang-wrapper{
    left:10px;
    color: dodgerblue;
}
.lang .lang-option{
    position: absolute;
    top: 115px;
    left: 0;
    z-index: 100;
    width: 100%;
    padding: 10px 0;
    background: ghostwhite;
    border-top: 1px solid cornflowerblue;
}
.lang-option .lang-selection{
    position: relative;
    height: 30px;
    cursor: pointer;
}
.lang-option .lang-selection:hover{
    background: lavender;
}
.lang-option .lang-selection i{
    position: absolute;
    top: 3px;
    left: 10px;
}
.lang-option .lang-selection .lang-sel-text{
    position: absolute;
    top: 3px;
    left: 40px;
}

div.user{
    float: right;
    width: 120px;
    margin-right: 50px;
}
.user:hover{
    background: ghostwhite;
}
.user .user-wrapper{
    padding: 0 10px;
    color: dodgerblue;
    cursor: pointer;
}
.user .user-wrapper .user-text{
    display: inline-block;
    position: absolute;
    left: 38px;
    max-width: 78px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: break-all;
}
.user-drawer .user-img-row{
    margin: 15px 0;
}
.user-drawer .user-img-row img{
    display: block;
    width:200px;
    height: 200px;
    margin: 10px auto;
    border-radius: 50%;
}
.user-drawer .upload-user-comp{
    position: absolute;
    top: 190px;
    right: 10px;
}
.user-drawer .upload-user-comp .upload-user-btn{
    max-width: 100px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: break-all;
}
.user-drawer .user-name{
    text-align: center;
    font-size: 24px;
    color: darkturquoise;
}
.user-drawer .title{
    font-size: 20px;
    font-weight: bold;
    font-style: italic;
    padding: 15px 0;
}
.user-drawer .user-favorite .user-favor-list{
    display: inline-block;
    margin: 0 0 10px 15px;
}
.user-drawer .user-grade .star-area{
    display: inline-block;
    position: relative;
    padding: 0 15px;
}
.user-drawer .user-grade .star{
    display: inline-block;
    color: #f5a623;
}
.user-drawer .user-grade{
    position: relative;
}
.user-drawer .user-grade .gradeNumber{
    position: absolute;
    right: -15px;
    top: 2px;
    color: #f5a623;
}
.user-drawer .star-area-fullfill{
    position: absolute;
    bottom: 0;
    overflow: hidden;
    white-space: nowrap;
}
.user-drawer .ranking{
    padding: 10px 15px;
    color:lightskyblue;
}

.index-page{
    position: relative;
    min-height: calc(100vh - 60px);
    background: aliceblue;
}
.index-page .index-backImg{
    display: block;
    width: 100vw;
    height: calc(100vh - 60px);
    background-size: cover;
}
.index-page .img-intro{
    position: absolute;
    top: 30vh;
    left: 0;
    right: 0;
    min-width: 500px;
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px 50px;
    color: #fff;
}
.index-page .img-title{
    font-size: 36px;
    height: 60px;
    line-height: 50px;
}
.index-page .img-text{
    font-size: 22px;
}
.cardArea{
    margin-top: 50px;
}
.cardArea .tabBtnAdd{
    cursor: pointer;
}

.index-page-module{
    margin: 10px 20px;
    cursor: pointer;
}
.index-page-module .space-module{
    height: 30vh;
}
.index-page-module .space-module .ivu-col-span-16{
    border: 1px solid #eee;
    border-top: 0;
    border-bottom: 0;
}
.index-page-module .space-module .ivu-col{
    padding: 10px 20px;
}
.index-page-module .space-module .space-desc{
    white-space: pre-wrap;
    text-align: left;
}
.index-page-module .card-theme{
    padding: 10px;
}
.index-page-module .card-theme .ivu-card{
    height: 30vh;
}
.index-page-module .card-title{
    color: #3cc;
}
.index-page-module .space-module .img-split{
    height: 100%;
}
.index-page-module .space-module .img-split>img{
    display: block;
    float: left;
    width: 20%;
    height: 100%;
    border-radius: 50%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 10px;
    -o-object-fit: cover;
       object-fit: cover;
    background: azure;
    -webkit-transition: -webkit-transform .2s linear;
    transition: -webkit-transform .2s linear;
    transition: transform .2s linear;
    transition: transform .2s linear, -webkit-transform .2s linear;
}
.index-page-module .space-module .img-split>img:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.index-page-module .cardThemeContent .title{
    padding-bottom: 20px;
    font-size: 20px;
    font-weight: bold;
}
.index-page-module .cardThemeContent .text{
    white-space: pre-wrap;
}
